<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div class="container">
      <div id="block1">Текст</div>
      <div id="block2">Текст</div>
    </div>
    <div>
      <p>{{ ControlCommand }}</p>
      <p>{{ Id }}</p>
      <p>{{ Name }}</p>
      <ul>
        <li v v-for="item in list" :key="item.id">
          <p>{{ item.name }}</p>
          <p>{{ item.online }}</p>
        </li>
      </ul>
    </div>
  </div>
</template>


<script>

import axios from 'axios';

export default{
  props:{
    server_addr:{
          type: String,
          default: 'localhos:8080'
      }
  },
  data(){
    return{
      ControlCommand: "",
      Id: 0,
      UserName: "",
      Name: "",
      Surname:"",
      Familyname: "",
      list:[{
                id:0,
                name: "",
                online: false,
            }],
    }
  },
  mounted(){
    axios.get('http://'+this.server_addr + '/about', {responseType:'arraybuffer'})
            .then((resp) =>{
                console.log(resp)
                this.ControlCommand = this.setCommand(resp.data)
            })
            .catch((err) =>{
                if (err.message == 'Network Error'){
                    return
                }
                if (err.response.status == 401){
                    this.$router.push('/login')
                }
                return 'false'
            })
  },
  methods:{
    setCommand(raw){
      switch (raw[0]){
        case 0:
          return "connect";
        case 1:
          return "disconnect";
        case 2:
          return "registration";
        case 3:
          return "user update";
        case 4:
          return "user info";
        case 5:
          return "user list";
        default:
          return "unknown command";      
      }
    }
  }
}

</script>

<style scoped>
.container {
  display: flex;
}

#block1, #block2 {
  flex: 1;
  margin: 2px;
  border: 1px solid blue;
}
</style>